/**
 * Dashboard API functions for testing
 * These are simplified versions for unit testing
 */

export interface DashboardData {
  agents: any[];
  metrics: any;
  knowledge: any;
}

export interface DashboardLayout {
  type: string;
  panels: any[];
}

export interface DashboardPreferences {
  theme: string;
  autoRefresh: boolean;
  refreshInterval: number;
  notifications?: any;
}

export async function getDashboardData(filters?: any): Promise<DashboardData> {
  const params = new URLSearchParams();
  if (filters) {
    Object.entries(filters).forEach(([key, value]) => {
      if (Array.isArray(value)) {
        params.set(key, value.join(","));
      } else {
        params.set(key, String(value));
      }
    });
  }

  const response = await fetch(
    `/api/dashboard${params.toString() ? "?" + params : ""}`,
    {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    },
  );

  if (!response.ok) {
    throw new Error(
      `Failed to fetch dashboard data: ${response.status} ${response.statusText}`,
    );
  }

  return response.json();
}

export async function updateDashboardLayout(
  layout: DashboardLayout,
): Promise<any> {
  // Validate layout
  if (
    !layout.type ||
    !["bloomberg", "resizable", "knowledge", "ceo-demo"].includes(layout.type)
  ) {
    throw new Error("Invalid layout configuration");
  }

  const response = await fetch("/api/dashboard/layout", {
    method: "PUT",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(layout),
  });

  return response.json();
}

export async function saveDashboardPreferences(
  preferences: DashboardPreferences,
): Promise<any> {
  // Validate preferences
  if (
    preferences.theme &&
    !["light", "dark", "auto"].includes(preferences.theme)
  ) {
    throw new Error("Invalid preferences");
  }
  if (preferences.refreshInterval && preferences.refreshInterval < 0) {
    throw new Error("Invalid preferences");
  }

  const body = {
    ...preferences,
    encrypted: true,
  };

  const response = await fetch("/api/dashboard/preferences", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(body),
  });

  return response.json();
}

export async function exportDashboardConfig(): Promise<Blob> {
  const response = await fetch("/api/dashboard/export", {
    method: "GET",
  });

  return response.blob();
}

export async function importDashboardConfig(config: any): Promise<any> {
  // Validate version
  if (!config.version || config.version < "1.0") {
    throw new Error("Configuration version not supported");
  }

  const response = await fetch("/api/dashboard/import", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify(config),
  });

  return response.json();
}

// Cache for metrics
const metricsCache = new Map();

export async function getAgentMetrics(options: any = {}): Promise<any> {
  const cacheKey = JSON.stringify(options);

  if (options.useCache && metricsCache.has(cacheKey)) {
    return metricsCache.get(cacheKey);
  }

  const params = new URLSearchParams();
  if (options.page) params.set("page", options.page);
  if (options.pageSize) params.set("pageSize", options.pageSize);

  const response = await fetch(`/api/agents/metrics?${params}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
    },
  });

  const data = await response.json();

  if (options.useCache) {
    metricsCache.set(cacheKey, data);
  }

  return data;
}

export async function getSystemHealth(): Promise<any> {
  const response = await fetch("/api/system/health", {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
    },
  });

  return response.json();
}

export async function subscribeToUpdates(
  type: string,
  callback: Function,
): Promise<any> {
  const response = await fetch("/api/subscriptions", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      type,
      events: ["agent-update", "metrics-update", "system-status"],
    }),
  });

  if (!response.ok) {
    const error = await response.json();
    throw new Error(error.error || "Subscription failed");
  }

  return response.json();
}

export async function unsubscribeFromUpdates(
  subscriptionId: string,
): Promise<void> {
  await fetch(`/api/subscriptions/${subscriptionId}`, {
    method: "DELETE",
  });
}
